nav{
    display: flex;
    align-items: center;
    gap: 1em;
    padding: 1.5em;
}

nav>a{
    color: white;
    text-decoration: none;
    font-size: 1.3rem;
    text-shadow: 3px 3px black;
}

nav>a:hover{
    color: green;
    text-decoration: 2px underline;
}


.contact{
    background-color: white;
    padding: 1em;
    border-radius: 15px;
    color: black;
    text-shadow: none;
}
